*{
    box-sizing: border-box;
}
body{
    margin: 0;
}
.nav_item {
    background-color: #253560;
    color:white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
    border: 1px solid #253D3D;
  }

  .title{
    font-size: 2rem;
  }

  .nav_item ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
  }

  .nav_item a{
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0.25rem;
  }

  .nav_item li.active {
    background-color: #1D2A4B ;
  }

  .nav_item li:hover {
    background-color: #4A65AD ;
  }
.bar{
    padding: 0.25rem;
    width: 500px;
}

.displayS{
    max-height : 0;
    visibility: visible;
    padding: 0.25rem;
    background-color: #1D2A4B;
    width: fit-content;
    margin-left: 380px;
    margin-top: -38px;
    align-items: center;
}

 p{
    align-items: center;
    padding: 0.25rem;
    color: white;
 }
  